//greyscale colors
$gsbg : #D8D8D8;
$gsnavbar : #B8B4B4;
$gstopbar : #B5B2B2;
$gsnavactive : #F4F4F4;
$gscontentbg : #F4F4F4;
$gscontentitem: #EBE6E6;
$gscontentbutton : #C4C4C4;
$gscontentNavbar : #E5E5E5;

$textsizeL: 18px;
$textsizeM: 16px;
$textsizeS: 13px;

html { margin-left: calc(100vw - 100%); }
body{
    background-color:$gsbg;
    font-family:'Roboto',sans-serif;
    font-size: $textsizeM;
}


h1,h2,p{
    padding:1rem;
}
h1{
    font-size:3rem;
}
h2{
    font-size:2rem;
}
a, a:visited, a:hover, a:active {
    color: inherit;
    text-decoration:none;
}
input, textarea,p,label{
    font-size:inherit;
}

.main{
    display:flex;
    flex-direction:column;
    justify-content:center;
    flex-wrap:wrap;
}

.topBar{
    display:flex;
    background-color:$gstopbar;
    height:60px;
    justify-content:center;
    .logo-image{
        min-width:60px;
        min-height:60px;
    }
}

.navBar{
    background-color:$gsnavbar;
    display:flex;
    flex-direction:column;
    justify-content:center;
    text-align:center;
}


.navLinks{
    padding: 1rem 0;
    display:flex;
    flex-direction: column;            
    border:1px;
        .navLink {
            font-size:1.5rem;
            padding:1.5rem 0rem;
    transition-duration:0.4s;
    }
    .navLink:hover{
        background-color:$gscontentbg;
    }

}



.buttn{
    border:none;
    padding:15px 32px;
    font-size: $textsizeL;
    margin-bottom:20px;
    background-color:$gscontentbutton;
    transition-duration:0.4s;
}

.buttn:active,.buttn:hover,.links:hover,.links:active{
    background-color: $gscontentitem;
}

.avatar{
    .avatar-image{
        min-height:100px;
        min-width:100px;
    }
}

.links{
    align-self:flex-end;
    padding:0.5rem;
    margin:0.5rem;
    transition-duration:0.4s;
    background-color:$gscontentbutton;
}

.is-active{
    background-color: $gscontentbg;
}

// Content
.statusBar{
    height:32px;
    background-color: $gscontentNavbar;
    display:flex;
    justify-content: flex-end;
    .lang{
        border:none;
        background-color:$gscontentNavbar;
    }
.is-active{
    background-color: $gscontentbg;
}
    .lang:hover{
        background-color: $gscontentbg;
    }
}

#content{
    background-color: $gscontentbg;
}

.content-item{
    background-color: $gscontentNavbar;
    margin:1rem;
    display:flex;
    flex-direction:column;
}


// Home 
.home{
    display:flex;
    flex-direction:column;
    flex-wrap:wrap;
    .welcome{
        a{
            align-self:center;
            max-width:15rem;
        }
            text-align: center;
        h1{
            padding:2rem;
        }
        p{
            font-size:$textsizeL;
        }
 
    }
    .latest-blog{
        flex: 0 0 50%;
    }

    .fluff{
        flex: 1 0 0;
        min-width:4rem;
        min-height:10rem;
    }

}
// About
.about{
    flex-direction:row;
    flex-wrap: wrap;
    justify-content:center;
    .about-picture{
        display:flex;
        img{
            text-align:center;
            min-height:200px;
            min-width:200px;
        }
    }

    .about-facts{
        padding:1rem;
        background-color: $gscontentbutton;
    }

    .about-contact{
        margin:1rem;
        display:flex;
        justify-content:center;
        flex-direction: column;
        text-align:center;
        a{
            align-self:center;
        }
        p{
        padding:0rem;
        }
    }
}


// Blog
.blogPost{
    p{
        line-height:24px;
    }
}
// Contact
.contact-form{
    padding:1rem;
    display:flex;
    flex-direction:column;
    button{
        max-width:10rem;
        margin: 1rem 1rem;
    }

}

.contact-input{
    max-width:15rem;
    background-color:$gscontentbg;
    margin:0 1rem;
    padding:12px;
}
#message{
    resize:vertical;
    min-height:5rem;
    background-color: $gscontentbg;
    margin:0 1rem;
    padding:12px;
}


// Media queries

@media (min-width: 640px){
    .main{
        flex-direction:row;
        max-width:1200px;
        margin:auto;
        margin-top:30px;
    }
    
   
    #content{
        flex: 1 0 0;
    }
    .navBar{
        flex:0 0  15%;
        min-height: 80vh ;
        justify-content:flex-start;
        .colorTheme{
            margin-top: auto;
        }
        
    }
    .topBar{
        flex:0 0 100%;
        justify-content:flex-start;
    }

    .home{
        flex-direction:row;

    }

    .about-intro{
        flex:0 0 50%;
    }
    .about-picture{
        flex:1 0 0;
    }
    .about-history{
        flex:0 0 75%;
    }
    .about-facts{
        flex:1 0 0;
    }


        
}
        